@import "../../styles/settings";

.Dropdown {
  margin: 1px;
  background: none;
  border: 1px solid;
  border-color: #999;
  border-radius: 0.375em;
  color: $color-base-gray;
  font-weight: 400;
  min-width: 13em;
  position: relative;

  &.isOpen {
    border-radius: 0.375em 0.375em 0 0;
    border: none;
    box-shadow: 0 0 1em 0 #E0E0E0;
  }

  &--hidden {
    display: none;
  }

  &__content {
    background-color: $color-base-white;
    border-radius: 0 0 0.375em 0.375em;
    box-shadow: 0 0 1em 0 #E0E0E0;
    min-width: 100%;
    overflow: hidden;
    position: absolute;
    z-index: 2;
  }
}



