@import "settings";
@import "tools";

:root {
  min-width: 75em;
}

h2,
h3,
h4,
h5,
h6 {
  margin: 0.25em 0;
}

h1 {
  @include rem-fallback(font-size, 3.5);
  font-weight: 700;
}

h2 {
  @include rem-fallback(font-size, 3.5);
  font-weight: 300;
}

h3 {
  @include rem-fallback(font-size, 2.25);
  font-weight: 300;
}

a {
  color: inherit;
}

strong {
  font-weight: 700;
}

b {
  font-weight: 600;
}

input {
  border: 2px solid $color-base-gray-l;
  border-radius: 0.25em;
}

input:focus {
  border-color: $color-base-green;
}

input:invalid {
  border-color: $color-base-red;
}

input:disabled {
  background: $color-base-gray-l;
  color: $color-base-gray;
  border: none;
}

button,
input {
  outline: none;
}

button {
  cursor: pointer;
  font-weight: 400;
}

button:disabled {
  cursor: auto;
}



