.RadioButton {
  &__overlay {
    align-items: center;
    text-align: center;
    padding: 0.75em 0.875em;
    border-radius: 0.25em;
    line-height: 1em;
    display: inline-flex;
    min-width: 5em;
    margin: 0 1em 0 0;
    background: none;
    border: 1px solid;
    font-weight: 400;
  }

  &--hidden {
    display: none;
  }
}

.RadioButton__input:checked + .RadioButton__overlay {
  border-width: 3px;
}


