.OnboardingTooltipNavigation {
  border-top: 1px solid #F2F2F2;
  padding: 0.75em 2em;
  display: flex;
  justify-content: space-between;
    
  &__steps {
    font-size: 0.75rem;
    align-self: center;
    color: #999;
    margin: 0 2em;
    cursor: pointer;
  }

  &__directionNav {
    font-weight: 500;
    cursor: pointer;
  }

  &__closeIcon {
    color: #999;
    top: 1em;
    right: 1.25em;
    position: absolute;
    cursor: pointer;
  }
}

.OnboardingTooltipNavigation__step + .OnboardingTooltipNavigation__step {
  margin-left: 1em;
}


