@import '../../styles/settings';

.TreeNav {
  .label {
    flex: 1;
    text-align: left;
  }
  &__subNav {
  	padding-left: 1em;
  }

  &__dropdown:hover {
    background: $color-base-gray-l;
  }

  .Menu__item {
    &.selected {
      font-weight: 600;
    }
    
    .Menu__item__check {
      display: none;
    }
  }
}


