@import '../../styles/settings';

.Toggle {
  display: inline-flex;

  &__overlay {
    cursor: pointer;
    border: none;
    border-radius: 1em;
    min-width: 2.5em;
    width: 2.5em;
    height: 1.5em;
    background: $color-base-gray;

    &::before {
      content: " ";
      display: inline-block;
      width: 1.5em;
      height: 1.5em;
      border-radius: 1em;
      border: 3px solid $color-base-gray;
      background: white;
      box-sizing: inherit;
    }
  }

  &--hidden {
    display: none;
  }

}

.Toggle__input:checked + .Toggle__overlay  {
  background: $color-base-green;
}

.Toggle__input:checked + .Toggle__overlay::before {
  margin: 0 1em;
  border-color: $color-base-green;
}


