@import "../../styles/settings";

.Selector {
  border-radius: 0.375em;
  border-color: #999;
  background: none;
  min-width: 13em;
  border: 1px solid;
  font-weight: 400;

  &__button {
    width: 100%;
  }

  &__buttonContent {
    width: 100%;

    padding: 0.625em 1.55em;
    line-height: 1em;
    display: inline-flex;
    color: #333;
    font-weight: 500;
    border-radius: 0.375em;
  }

  &.isOpen {
    .Button {
      border-radius: inherit;
      background-color: $color-base-white;
      position: relative;
      z-index: 3;
    }
  }

  .label {
    flex: 1;
    text-align: left;
  }
}


