@import '../../styles/settings';

.SearchField {
  &__input {
    appearance: none;
    padding: 0.7em 1em 0.7em 3.2em;
    border-radius: 0.25em;
    border: 2px solid $color-base-gray-l;
    width: 100%;
  }
  .fa {
    margin: 1.1em 1.3em;
    position: absolute;
  }
  &__icon {
    color: $color-base-gray;
  }
}



