@import '../../styles/settings';

.Tabs {
  background-color: $color-base-semi-white;
  &__background {
    background-color: $color-base-gray-l;
  }

  &__tabs {
    text-transform: uppercase;
    display: flex;
    flex-direction: row;
    list-style: none;
    font-size: 1em;
  }

  &__content {
    box-shadow: 0px -1px 3px 0px #E0E0E0;
    padding: 2em 0 1em;
  }

  &__tab {
    color: #666;
    list-style: none;
    padding: 0.8em 2em;
    margin: 0 1em 0 0;
    border-radius: 0.25em 0.25em 0 0;
    cursor: pointer;
  }

  &__tab:hover {
    background: darken( $color-base-gray-l, 5%);
  }

  &__tab--active {
    color: #333;
    box-shadow: 0px -3px 3px 0px #E0E0E0;
    background: $color-base-semi-white;
  }

  &__tab--active:hover {
    background: $color-base-semi-white;
  }

  &__icon {
    margin: 0 0.5em 0 0;
  }
}



