.Tooltip {
  display: flex;
  flex-direction: column;
  max-width: 34em;
  background: rgba(white, 0.95);
  border-radius: 0.375em;
  box-shadow: 0 0 1.5em 0 #E0E0E0;

  &__info {
    padding: 1.5em 2em;
  }

  &__title {
    margin-top: 0.33em;
    line-height: 2em;
    font-size: 1.25rem;
    font-weight: 400;
  }

  &__description {
    line-height: 1.25em;
    font-weight: 300;
    font-size: 14px;
  }

  &--visible {
    opacity: 0.9;
  }

  opacity: 0;
  transition: opacity 1s;
}



