@import 'variables';
@import 'mixins';

.container-fluid {
  @include container-fluid();
  max-width: 85em;
  min-width: 75em;
}

.align-baseline {
  display: flex;
  align-items: baseline;
}

.align-center {
  display: flex;
  align-items: center;
}

.justify-center {
  display: flex;
  justify-content: center;
}

.justify-right {
  display: flex;
  justify-content: flex-end;
}

.row {
  @include row();
}

%column {
  @include column();
}

@include columns() {
  @extend %column;
}

@include offsets() {
  @extend %column;
}

@include breakpoints('.start') {
  justify-content: flex-start;
}

@include breakpoints('.center') {
  justify-content: center;
}

@include breakpoints('.end') {
  justify-content: flex-end;
}

@include breakpoints('.top') {
  align-items: flex-start;
}

@include breakpoints('.middle') {
  align-items: center;
}

@include breakpoints('.bottom') {
  align-items: flex-end;
}

@include breakpoints('.around') {
  justify-content: space-around;
}

@include breakpoints('.between') {
  justify-content: space-between;
}

@include breakpoints('.first') {
  order: -1;
}

@include breakpoints('.last') {
  order: 1;
}

@include breakpoints('.reverse') {
  flex-direction: row-reverse;
}




