@import "../base/settings";
@import "../base/tools";

.stretch {
  width: 100%;
}

.outline {
  border: 1px solid;
  font-weight: 400;
}

.border {
  border: 2px solid;
}

.dark-gray {
  color: #333;
  border-color: #666;
}

.gray {
  color: $color-base-gray;
}

.green {
  color: $color-base-green;
}

.blue {
  color: $color-base-blue;
}

.yellow {
  color: $color-base-yellow;
}

.teal {
  color: $color-base-teal;
}

.red {
  color: $color-base-red;
}

.white {
  color: $color-base-white;
}

.green.cta {
  color: $color-base-white;
  background: $color-base-green;
}

.outline {
  background: none;
}

.outline.cta {
  color: $color-base-green;
  background: none;
}

.green.outline:hover {
  color: $color-base-text-gray;
  background: none;
}

.green.outline.cta:hover {
  color: $color-base-green-l;
  background: none;
}

.green.outline.cta:active {
  color: $color-base-green-d;
  background: none;
}

.green.cta:hover {
  background: $color-base-green-d;
}

.green.cta:active {
  background: $color-base-green-d;
}

.neutral.outlined {
  background: none;
}

.neutral {
  color: $color-base-white;
  border: 2px solid $color-base-white;
  background: $color-base-white;
}

.neutral.outlined {
  background: none;
}

.subhead-1,
.subhead-2 {
  margin: 1em 0;
}

.subhead-1 {
  @include rem-fallback(font-size, 1.125);
  font-weight: 600;
}

.subhead-2 {
  @include rem-fallback(font-size, 1.125);
  font-weight: 300;
}

.caption {
  @include rem-fallback(font-size, 0.75);
}

.is-hidden {
  display: none;
}

.text-align--right {
  &.Button{
    text-align: right;
  }
}

.flx-stretch {
  flex: 1;
}

.nowrap {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.row--center,
.column--center {
  align-self: center;
}

.flex--horizontal--center {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.flex--vertical--center {
  display: flex;
  align-items: center;
  flex-direction: column;
}



