@import '../../styles/settings';

.Menu {
  background: white;
  list-style: none;
  padding: 0 0 0.5em;

  &__item {
    display: flex;
    padding: 0.5em 1.55em;
    cursor: pointer;
    line-height: 1em;
    width: 100%;

    &__check {
      color: $color-base-green;
    }
  }

  &__item:hover {
    background: $color-base-gray-l;
  }
}

.MenuItem {
  &__label {
    flex: 1;
  }
}


