@import '../../styles/settings';

.Alert {
  transition: all 0.4s cubic-bezier(0.25, 0.8, 0.25, 1);
  opacity: 0.9;
  position: fixed;
  z-index: 99999;
  padding: 2em;
  text-align: center;
  color: $color-base-semi-white;
  a {
    text-decoration: underline;
  }
  &__error {
    background-color: $color-base-red;
  }
  &__success {
    background-color: $color-base-green;
  }
  &__notify {
    background-color: $color-base-yellow;
  }

  &__position-bottom-left {
    transform: translate(-100%, 0);
    left: 0;
    bottom: 1em;
    &.Alert__visible {
      left: 1em;
      bottom: 1em;
    }
  }

  &__position-bottom-right {
    transform: translate(100%, 0);
    right: 0;
    bottom: 1em;
    &.Alert__visible {
      right: 1em;
      bottom: 1em;
    }
  }

  &__position-top-left {
    transform: translate(-100%, 0);
    left: 0;
    top: 1em;
    &.Alert__visible {
      left: 1em;
      top: 1em;
    }
  }

    &__position-top-right {
    transform: translate(100%, 0);
    right: 0;
    top: 1em;
    &.Alert__visible {
      right: 1em;
      top: 1em;
    }
  }

  &__visible {
    transform: translate(0, 0);
  }

}



