.Onboarding {
  &--covered {
    pointer-events: none;
    cursor: default;
  }

  &__wrapper {
    position: relative;
  }

  &__overlay {
      &--active {
      opacity: .8;
      top: 0;
      left: 0;
      pointer-events: none;
      cursor: default;
      background: white;
      position: absolute;
      width: 100%;
      height: 100%;
    }
  }

  &__highlight {
    &--active {
      z-index: 1;
      position: relative;
    }
  }
}



