@import '../../styles/settings';

.Checkbox {
  display: inline-flex;
  align-items: center;

  &__overlay {
    width: 1em;
    height: 1em;
    line-height: 1em;
    margin: 0.15em 0.5em;
    border-radius: 0.2em;
    box-shadow: 0 0 0 1px $color-base-gray inset;
    text-align: center;
    cursor: pointer;
  }

  &--hidden {
    display: none;
  }
}

.Checkbox__input:checked + .Checkbox__overlay {
  background: $color-base-gray;
}

.Checkbox__input:checked + .Checkbox__overlay::before {
  content: "\f00c";
  color: white;
  font-family: FontAwesome;
  font-size: 0.75em;
}




