@import '../../styles/settings';

.Paginator {
  display: flex;
  padding: 2em;
  justify-content: center;
  color: $color-base-text-gray-l;

  li {
    list-style: none;
    &:hover {
      color: mix($color-base-white, $color-base-text-gray-l);
      background: none;
    }
  }

  &__unusable {
    color: mix($color-base-white, $color-base-text-gray-l);
    background: none;
    pointer-events: none;
  }

  &__page {
    line-height: 1.1em;
    padding: 0 0.5em;

    &--active {
      color: $color-base-green;
      border: 2px solid;
      border-radius: 0.33em;
    }
  }
}

.Paginator__page + .Paginator__page {
  margin: 0 0 0 0.75em;
}


