@import '../../styles/settings';

.Panel {
  border: 1px solid #E6E6E6;
  border-radius: 0.25em;
  background-color: $color-base-white;
  position: relative;

  &__headerContent {
    margin: 0 2em;
    flex: 1;
  }

  &__header {
    border-bottom: 1px solid #EEE;
    display: flex;
    padding: 1em 1.5em 0.75em;
    align-items: center;
  }

  /* TODO make this better */
  &__borderlessHeader {
    position: absolute;
    right: 1.5em;
    top: 1em;
  }

  &__title {
    font-size: 1rem;
    font-weight: 500;
  }

  &__help {
    color: #999;
  }
}



